import { format } from 'date-fns';
import { sentenceCase } from 'change-case';
import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Card, Stack, Avatar, AvatarGroup, Divider, Badge, Typography } from '@mui/material';
//
import Label from '../../Label';
import Scrollbar from '../../Scrollbar';
// ----------------------------------------------------------------------
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor: '#44b700',
    color: '#44b700'
  }
}));

export default function BookingDetails() {
  const theme = useTheme();

  return (
    <>
      <Card sx={{ px: 3, py: 1 }}>
        <Scrollbar>
          <Stack
            direction="row"
            alignItems="center"
            spacing={2}
            sx={{ minWidth: 800 }}
            divider={<Divider orientation="vertical" flexItem />}
          >
            <Stack spacing={1.5}>
              <Stack direction="row" alignItems="center" spacing={3}>
                <Stack direction="column">
                  <Typography variant="h6">June 4th 2021</Typography>
                  <Typography variant="subtitle2" sx={{ color: '#637381', fontSize: '10px' }}>
                    Enq id # 0011223344
                  </Typography>
                </Stack>
              </Stack>
            </Stack>

            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px' }}>
                Items
              </Typography>
              <Stack direction="row" spacing={0.5}>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_3.jpg"
                    style={{
                      width: '40px',
                      height: '40px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_1.jpg"
                    style={{
                      width: '40px',
                      height: '40px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_9.jpg"
                    style={{
                      width: '40px',
                      height: '40px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
              </Stack>
            </Stack>

            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px' }}>
                Status
              </Typography>
              <Label variant="filled" style={{ backgroundColor: '#FFF2E2', color: '#F2C94C', fontSize: '10px' }}>
                {sentenceCase('pending')}
              </Label>
            </Stack>

            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px' }}>
                Follow up Task Date
              </Typography>
              <Stack direction="row" spacing={0.5} sx={{ fontSize: '10px' }}>
                {format(new Date(), 'MMM dd, yyyy')}
                <Label variant="filled" style={{ backgroundColor: '#FD938D' }}>
                  {sentenceCase('2 Days Delay')}
                </Label>
              </Stack>
            </Stack>

            <Stack direction="row" spacing={2.5} sx={{ flexGrow: 1 }}>
              <Stack>
                <Typography variant="subtitle1" sx={{ fontSize: '12px' }}>
                  Hosts
                </Typography>
                <AvatarGroup max={4}>
                  <Avatar alt="My Avatar" src="/static/mock-images/avatars/avatar_3.jpg" />
                  <Avatar alt="My Avatar" src="/static/mock-images/avatars/avatar_6.jpg" />
                  <Avatar alt="My Avatar" src="/static/mock-images/avatars/avatar_5.jpg" />
                  <Avatar alt="My Avatar" src="/static/mock-images/avatars/avatar_3.jpg" />
                  <Avatar alt="My Avatar" src="/static/mock-images/avatars/avatar_3.jpg" />
                </AvatarGroup>
              </Stack>

              <Icon
                icon="mdi:chevron-right"
                style={{ color: '#916A00', transform: 'scale(3)', alignItems: 'center', justifyContent: 'flex-end' }}
              />
            </Stack>
          </Stack>
        </Scrollbar>
      </Card>
    </>
  );
}
